/* Lib - Local component imports
/* ---------------------------------------------------------- */

/* Base components */

@import "components/forms.css";
@import "components/buttons.css";
/* Ghost components */

@import "ghost/header.css";
@import "ghost/content.css";
@import "ghost/readmore.css";
@import "ghost/members.css";
@import "ghost/errors.css";
@import "ghost/footer.css";
@import "ghost/badge.css";
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap');
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
/* Rebuild base elements like lists or anchors or video players
/* ---------------------------------------------------------- */

.subscription-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 30rem;
}

.subscription-form input,
.subscription-form button {
  padding: 10px;
  border-radius: 5px;
  box-shadow: none;
  border: 1px solid #cecece;
  margin-bottom: 1em;
}

.subscription-form .success-message,
.subscription-form .error-message {
  display: none;
}

.subscription-form.error .error-message,
.subscription-form.success .success-message {
  display: block
}

.subscription-box {
    background-color: lightyellow;
    padding: 2em;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    border-radius: 5px;
}


/* Progress bar */
#progress-bar {
  --scrollAmount: 0%;
  background-image: linear-gradient(120deg, #61F0A9 0%, #8fd3f4 100%);
  width: var(--scrollAmount);
  height: 7px;
  position: fixed;
  top: 0;
}

ol li{
    list-style-type: decimal;
    list-style: decimal;
}
code {
    background-color: #F0F061;
    padding: 1px;
    color: #28282F;
}

li {
    list-style: initial;
}

a {
    color: #61F0A9 ;
}

a:hover {
    color: #F061A8 !important;
}

a:focus {
    color: #F061A8 !important;
}

.fluid-width-video-wrapper {
    position: relative;
    overflow: hidden;
    padding-top: 56.25%;
}

.fluid-width-video-wrapper iframe,
.fluid-width-video-wrapper object,
.fluid-width-video-wrapper embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.youtube-player {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
    background: #000;
    margin: 5px;
}

.youtube-player iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: transparent;
}

.youtube-player img {
    object-fit: cover;
    display: block;
    left: 0;
    bottom: 0;
    margin: auto;
    max-width: 100%;
    width: 100%;
    position: absolute;
    right: 0;
    top: 0;
    border: none;
    height: auto;
    cursor: pointer;
    -webkit-transition: 0.4s all;
    -moz-transition: 0.4s all;
    transition: 0.4s all;
}

.youtube-player img:hover {
    -webkit-filter: brightness(75%);
}

.youtube-player .play {
    height: 72px;
    width: 72px;
    left: 50%;
    top: 50%;
    margin-left: -36px;
    margin-top: -36px;
    position: absolute;
    background: url('//i.imgur.com/TxzC70f.png') no-repeat;
    cursor: pointer;
}


/* ---------------------------------------------------------- */

form[data-members-form] .message-loading, form[data-members-form] .message-success, form[data-members-form] .message-error {
    display: none;
}

form[data-members-form].loading .message-loading, form[data-members-form].success .message-success, form[data-members-form].error .message-error {
    display: block;
}

/* Main - Your styles here! Edit below
/* ---------------------------------------------------------- */

html, .gh-viewport {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: #fff;
}

.gh-main {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
}

/* .gh-page {
    padding: 0 4vmin;
} */

.gh-article {
    padding: 4vmin 0;
}

.gh-postfeed {
    padding: 4vmin 0;
}

.gh-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.gh-head-actions a {
    display: inline-flex;
    align-items: center;
    margin: 0;
    padding: 10px;
}

.gh-head-actions a svg {
    height: 1.8rem;
    fill: currentcolor;
}

.gh-head-actions a:first-child svg {
    margin-left: 20px;
}

.gh-head-actions a:last-child svg {
    height: 2rem;
}

/* Index
/* ---------------------------------------------------------- */

.gh-card:not(:first-child) {
    margin: 8vmin auto 0;
}

.gh-card-link {
    display: flex;
    flex-direction: column;
}

.gh-card-image {
    width: 100%;
    max-height: 700px;
    object-fit: cover;
    margin-bottom: 2vmin;
}

.gh-card-meta {
    display: block;
    margin: 0 0 1rem;
    font-size: 1.4rem;
    line-height: 1.2em;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    opacity: 0.8;
}

.gh-card-content {
    max-width: 720px;
    margin: 0 auto;
    text-align: center;
    font-size: 1.6rem;
}

.gh-card-content p {
    margin: 1.2rem 0;
    padding: 0 6vmin;
}

.gh-card-content>strong {
    display: inline-block;
    padding: 1px 0;
    font-weight: 500;
    box-shadow: 0 -1px 0 0 inset currentColor;
    opacity: 0.5;
}

/* Page
/* ---------------------------------------------------------- */

.gh-page-head {
    margin: 0 auto;
    padding: 3vmin 0 2vmin;
    text-align: center;
}

.gh-page-image {
    margin: 2vmin 0 0 0;
}

.gh-page-head>p {
    max-width: 720px;
    margin: 0.3em auto 0;
    padding: 0 3vmin;
    line-height: 1.4em;
}

@media (max-width: 700px) {
    .gh-page-head>p {
        font-size: 1.6rem;
    }
}

/* Post
/* ---------------------------------------------------------- */

.gh-header {
    padding: 0 0 4vmin 0;
    text-align: center;
}

.gh-content h1 {
    font-family: 'Inter', 'sans-serif';
    font-size: 2.25rem;
    line-height: 2.5rem;
    font-weight: 700;
}

.gh-content h2 {
    font-family: 'Inter', 'sans-serif';
    font-size: 1.875rem;
    line-height: 2.25rem;
       font-weight: 700;
}

.gh-content h3 {
    font-family: 'Inter', 'sans-serif';
    font-size: 1.5rem;
    line-height: 2rem;
       font-weight: 700;
}

.gh-post-meta {
    display: block;
    margin: 0 0 1rem;
    font-size: 1.4rem;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    opacity: 0.8;
}

.gh-excerpt {
    margin: 1rem 0 0 0;
}

.gh-feature-image {
    grid-column: wide-start / wide-end;
    width: 100%;
    margin: 6vmin 0 0;
}

.gh-post-footer {
    margin: 2rem 0;
}

/* Author Archive
/* ---------------------------------------------------------- */

.gh-author-image {
    height: 12vmin;
    width: 12vmin;
    margin: 0 auto 1.5em;
    border-radius: 100%;
    overflow: hidden;
    object-fit: cover;
}

.gh-author-meta {
    margin: 2vmin 0 0 0;
    font-size: 1.2em;
}

.gh-author-links {
    display: flex;
    align-items: center;
    justify-content: center;
}

.gh-author-links a {
    position: relative;
    display: inline-flex;
    align-items: center;
    margin: 0 0.75vmin;
}

.gh-author-links a+a:before {
    display: block;
    content: "";
    margin: 0 1.25vmin 0 0;
    height: 1em;
    width: 1px;
    background: rgba(0, 0, 0, 0.3);
    transform: rotate(20deg);
}

@media (max-width: 700px) {
    .gh-author-meta {
        font-size: 1.6rem;
    }
}